/** @type {import ("../../typing/phaser")} */
/** @type {import ("../../typing/SpinePlugin")} */
/** @type {import ("../../typing/spine")} */

import { CST } from "../Helper/CST";

export class MenuScene extends Phaser.Scene {
    constructor() {
        super({
            key: CST.SCENE.MENU_SCENE,
            pack: {
                files: [
                    { type: 'scenePlugin', key: 'SpinePlugin', url: 'SpinePlugin.min.js', sceneKey: 'spine' }
                ]
            }
        });
    }

    preload() {
        this.load.spine("girl", "./assets/spine/Girl/Girl_1.json", ["./assets/spine/Girl/Girl_1.atlas"]);
        this.load.spine("bjj", "./assets/spine/BJJ_HNS/BJJ_HNS.json", ["./assets/spine/BJJ_HNS/BJJ_HNS.atlas"]);
    }

    create() {
        let girl = this.add.spine(200, 600, "girl", "Idle", true);
        let bjj = this.add.spine(600, 550, "bjj", "Idle", true);
        bjj.setScale(0.3);
        let skeletonData = bjj.skeletonData;
        let skin = bjj.skeletonData.findSkin("default");
        let skinName = ["Bjj_Face", "Costumes/Bjj_Dress"];
        for (const item in skinName) {
            let tempSkin = skeletonData.findSkin(skinName[item]);
            skin.addSkin(tempSkin);
        }
        bjj.setSkin(skin);
        bjj.setSlotsToSetupPose();

        let text = this.add.text(5, 20, localStorage.getItem(CST.STORAGE_KEY.PLAYER_NAME),
        {
            color: 'white', fontFamily: 'BalooBold',
            fontSize: '30px', align: "left", color : "#ffffff"
        });
    }
}