/** @type {import ("../../typing/phaser")} */

import { CST } from "../Helper/CST";

export class NameScene extends Phaser.Scene {
    constructor() {
        super({
            key: CST.SCENE.NAME_SCENE
        });
    }

    preload() {
        this.load.image(CST.IMAGES.INPUT_BOX, "./assets/images/Popup/" + CST.IMAGES.INPUT_BOX);
        this.load.image(CST.IMAGES.NEXT_BUTTON, "./assets/images/Popup/" + CST.IMAGES.NEXT_BUTTON);
        this.load.image(CST.IMAGES.POUP_UP, "./assets/images/Popup/" + CST.IMAGES.POUP_UP);
        this.load.html(CST.HTML.NAME_INPUT, "./assets/text/" + CST.HTML.NAME_INPUT);
    }

    create() {
        this.add.image(this.game.renderer.width / 2, this.game.renderer.height / 2, CST.IMAGES.POUP_UP);

        let text = this.add.text(this.game.renderer.width / 2 - 255, this.game.renderer.height / 2 - 185, 'Enter Your Name',
        {
            color: 'white', fontFamily: 'BalooBold',
            fontSize: '70px', align: "left", color : "#834f2a"
        });

        this.add.image(this.game.renderer.width / 2, this.game.renderer.height / 2 - 10, CST.IMAGES.INPUT_BOX);
        let element = this.add.dom(this.game.renderer.width / 2 + 10, this.game.renderer.height / 2 - 10).createFromCache(CST.HTML.NAME_INPUT);

        let nextButton = this.add.image(this.game.renderer.width / 2, this.game.renderer.height / 2 + 150, CST.IMAGES.NEXT_BUTTON);

        nextButton.setInteractive();
        nextButton.on("pointerup", ()=> {
            let inputUsername = element.getChildByName('username');
            this.tweens.add({
                targets: nextButton,
                duration: 100,
                ease: 'Bounce',
                scaleX : 1.1,
                scaleY : 1.1,
                yoyo: true
            }).on("complete", ()=> {
                if(inputUsername.value != ""){
                    localStorage.setItem(CST.STORAGE_KEY.PLAYER_NAME, inputUsername.value);
                    this.scene.start(CST.SCENE.MENU_SCENE);
                }
            });
        });
    }
}