/** @type {import ("../../typing/phaser")} */

import { CST } from "../Helper/CST";

export class LoadScene extends Phaser.Scene {
    constructor() {
        super({
            key: CST.SCENE.LOAD_SCENE
        });
    }

    init() {
        //  Inject our CSS
        var element = document.createElement('style');

        document.head.appendChild(element);

        var sheet = element.sheet;

        var styles = '@font-face { font-family: "BalooBold"; src: url("assets/font/Baloo2-Bold.ttf") format("opentype"); }\n';

        sheet.insertRule(styles, 0);
    }

    preload() {
        this.load.script('webfont', 'https://ajax.googleapis.com/ajax/libs/webfont/1.6.26/webfont.js');
    }

    create() {
        var add = this.add;
        var input = this.input;

        WebFont.load({
            custom: {
                families: ['BalooBold']
            }
        });

        if (localStorage.getItem(CST.STORAGE_KEY.PLAYER_NAME) == null || localStorage.getItem(CST.STORAGE_KEY.PLAYER_NAME) == "") {
            this.scene.start(CST.SCENE.NAME_SCENE);
        }
        else {
            this.scene.start(CST.SCENE.MENU_SCENE);
        }
    }
}